.delegdate-details .details-box,
.delegdate-details .details-big-box {
  width: 781px;
  background: #f5f5f5;
  border-radius: 4px;
  margin-bottom: 24px;
  padding: 0 17px;
}
.delegdate-details .details-box .box-span,
.delegdate-details .details-big-box .box-span {
  font-size: 12px;
  font-weight: 400;
  color: rgba(0, 0, 0, 0.5);
}
.delegdate-details .details-box {
  height: 55px;
  line-height: 55px;
}
.delegdate-details .details-big-box {
  padding: 30px 17px;
}
.RenderNodeNameLabel {
  padding: 0 10px;
  display: grid;
  justify-items: start;
  align-items: start;
  grid-template-columns: 1fr;
  grid-template-rows: 14px 14px;
  grid-row-gap: 11px;
  line-height: 14px;
}
.RenderNodeNameLabel .RenderNodeNameLabelTop {
  font-size: 18px;
  font-weight: bold;
  color: #000000;
  line-height: 18px;
}
.RenderNodeNameLabel .RenderNodeNameLabelTop .colorRequire {
  color: red;
}
.RenderNodeNameLabel .RenderNodeNameLabelBottom {
  font-size: 12px;
  font-weight: 500;
  color: rgba(0, 0, 0, 0.5);
  line-height: 14px;
}
.RenderMachineLabel {
  padding: 0 10px;
  display: grid;
  justify-items: start;
  align-items: start;
  grid-template-columns: 1fr;
  grid-template-rows: 14px 14px;
  grid-row-gap: 11px;
  line-height: 14px;
}
.RenderMachineLabel .RenderMachineLabelTop {
  font-size: 18px;
  font-weight: bold;
  color: #000000;
  line-height: 18px;
}
.RenderMachineLabel .RenderMachineLabelTop .colorRequire {
  color: red;
}
.RenderMachineLabel .RenderMachineLabelBottom {
  font-size: 12px;
  font-weight: 500;
  color: rgba(0, 0, 0, 0.5);
  line-height: 14px;
}
@media only screen and (max-width: 768px) {
  .delegdate-details .details-box,
  .delegdate-details .details-big-box {
    width: 100%;
  }
}
